import { TenantRoutingModule } from './tenant-routing.module';
import { ThfModule } from '@totvs/thf-ui/thf.module';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { TenantComponent } from './tenant.component';
import { FormTenantComponent } from './form-tenant/form-tenant.component';
import { TenantService } from './tenant.service';
import { AuthService } from '../auth/auth.service';
import { TokenInterceptor } from '../auth/token.interceptor';
import { TenantThfLookupService } from './tenant.thflookup.service';

@NgModule({
  imports: [
    CommonModule,
    ThfModule,
    TenantRoutingModule,
    FormsModule,
    HttpClientModule
  ],
  declarations: [
    TenantComponent,
    FormTenantComponent
  ],
  providers: [
    TenantThfLookupService,
    TenantService,
    AuthService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: TokenInterceptor,
      multi: true
    }
  ]
})

/**
 * @description
 * Classe principal do modulo de tenant
 */
export class TenantModule { }



