import { FormTenantComponent } from './form-tenant/form-tenant.component';
import { TenantComponent } from './tenant.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const tenantRoutes: Routes = [
  {path: '', component: TenantComponent},
  {path: 'new', component: FormTenantComponent},
  {path: 'edit/:id', component: FormTenantComponent}
];

/**
 * @description
 * Módulo responsável por gerenciar as rotas do módulo de tenant
 */
@NgModule({
  imports: [RouterModule.forChild(tenantRoutes)],
  exports: [RouterModule]
})

/**
 * @description
 * A classe de roteamento do modulo de tenant
 */
export class TenantRoutingModule { }



