import { HubCrudComponentForm } from './../../utils/hub-crud-component-edit';
import { TenantService, TenantData } from './../tenant.service';
import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ThfCheckboxGroupOption } from '@totvs/thf-ui/components/thf-field';

@Component({
  selector: 'app-form-tenant',
  templateUrl: './form-tenant.component.html',
  styleUrls: ['./form-tenant.component.css']
})

/**
 * @description
 * A classe responsável pelas ações do formulário de tenant
 * @extends HubCrudComponentForm
 */
export class FormTenantComponent extends HubCrudComponentForm<TenantData, number> {
  public isActiveList: Array<ThfCheckboxGroupOption> = [{ value: 'isActive', label: 'Ativo' }];;
  public isActiveResponse: Array<any> = [];

  /**
   * O Contrutor para inicialização
   */
  constructor(protected activatedRoute: ActivatedRoute,
    public router: Router,
    public dataService: TenantService) {
    super(activatedRoute, router, dataService, TenantData);

    this.isActiveResponse = [];
    
    this.beforeEditing.subscribe(data => {
      this.isActiveResponse = data.isActive ? ['isActive'] : [];
    });
  }
}



